import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App.jsx'
import './index.css'
import { debug } from './utils/debug'

// Log that we're initializing the embedded app
debug('EMBED_INIT', {
  time: new Date().toISOString(),
  url: window.location.href,
  params: Object.fromEntries(new URLSearchParams(window.location.search).entries())
})

// Check if the root element exists
const rootElement = document.getElementById('root')
if (!rootElement) {
  console.error('Root element not found in embed.html')
}

// Initialize the React app
const root = ReactDOM.createRoot(rootElement)
root.render(
  <React.StrictMode>
    <App embedded={true} />
  </React.StrictMode>
)

// Notify the parent iframe that we're ready
try {
  window.parent.postMessage(
    { 
      type: 'DICTATION_APP_READY', 
      height: document.body.scrollHeight,
      status: 'success'
    }, 
    '*'
  )
} catch (e) {
  console.error('Could not communicate with parent frame:', e)
}
